import React from "react";
import { CustomColumns, Hotkey } from "../../types";
import { useQuery } from "react-query3";
import { Button, PageHeader, Space } from "antd";
import { Modal } from "components/ui/Modal";
import Table from "components/ui/Table";
import TagLink from "components/ui/TagLink";
import EditModal from "components/ui/EditModal";
import DeleteButton from "components/ui/DeleteButton";
import HotkeyModal from "./hotkeyModal";
import { LinkOutlined } from "@ant-design/icons";

const columns: CustomColumns<Hotkey>[] = [
  {
    title: "Script",
    key: "script",
    editable: false,
    render: (text, record) => (
      <TagLink
        to={`/admin/automation/scripts/${record?.script}`}
        text={record?.script}
      />
    ),
  },
  {
    title: "Hotkey",
    dataIndex: "hotkey",
    key: "keys",
    editable: false
  },
  {
    title: "Environment",
    key: "environment",
    dataIndex: "environment",
    editable: false,
    render: (text) => (text ? text : "Default"),
  },
  {
    key: "runAs",
    title: "Run As",
    editable: false,
    render: (text, record) => record?.credential || "Default",
  },
  {
    width: "fit-content",
    dataIndex: "actions",
    editable: false,
    render: (text, record) => {
      return (
        <Space>
          <Modal>
            <EditModal record={record} allowedWithOneWayGitSync={false} />
          </Modal>
          <DeleteButton resource={record} allowedWithOneWayGitSync={false} />
        </Space>
      );
    },
  },
];

export default function Hotkeys() {
  const { data: hotkeys, isLoading, refetch } = useQuery<Hotkey[]>(
    "/hotkey",
    {
      enabled: false,
      refetchOnWindowFocus: false,
    }
  );

  return (
    <PageHeader title="Hotkeys" subTitle="Keyboard shortcuts that execute PowerShell scripts." extra={[
      <Button icon={<LinkOutlined />} href="https://docs.powershelluniversal.com/automation/hotkey" target="_blank">Documentation</Button>
    ]}>
      <Table
        title="Hotkeys"
        actions={[
          <Modal>
            <HotkeyModal />
          </Modal>,
        ]}
        columns={columns}
        data={hotkeys}
        loading={isLoading}
        queryFn={refetch}
      />
    </PageHeader>

  );
}
