import React, { Fragment } from "react";
import { AccessControlTypes, Hotkey } from "types";
import { Button } from "antd";
import {
  ModalContent,
  ModalOpenButton,
  useModalContext,
} from "components/ui/Modal";
import { Form, FormContentBase } from "components/ui/Form/Form";

export default function HotkeyModal() {
  const { onOk, form } = useModalContext();

  function handleOk() {
    onOk("/hotkey", "create", {} as Hotkey);
  }

  return (
    <Fragment>
      <ModalOpenButton requiredRoles={["Administrator", "Operator"]} requiredAccessControls={AccessControlTypes.Create} allowedWithOneWayGitSync={false}>
        <Button children="Create New Hotkey" />
      </ModalOpenButton>
      <ModalContent title="Create new hotkey" onClickOk={handleOk} formName="create_hotkey_form">
        <Form schemaName="hotkey">
          <FormContentBase
            name="create_hotkey_form"
            form={form}
            preserve={false}
          />
        </Form>
      </ModalContent>
    </Fragment>
  );
}
